import * as handle from '../utils/handlers'
import N from '../../utils/tools/number'
import {sendMail} from '../utils'
import gtSlide from '../../static/gt-slide'

import {User} from "./user"

// getCode
export const getCode = async(ctx, next) => {
  let {username,email} = ctx.request.body

  try{
    if(!username || !email) ctx.throw('')
    let user = await User.findOne({username, email})
    if(!user) ctx.throw('')

    let n = ctx.session.views || 0
    ctx.session.views = ++n
    if(ctx.session.views > 3) ctx.throw('10')

    let code = N.randomCode(6)
    ctx.session.validateCode = code
    let html = `<div> <span style="font-size: 20px;color:#ff4c29;">${code}</span> 10</div>`
    await sendMail(email,'',html)
    handle.success(ctx)
  }catch (e) {
    handle.error(ctx,e)
  }
}

// sendEmail
export const sendEmail = async (ctx, next) => {
  let {to,subject,html} = ctx.request.body

  try{
    if(!to || !subject || !html) ctx.throw('')
    let res = await sendMail(to,subject,html)
    handle.data(ctx,res)
  }catch (e) {
    handle.error(ctx,e)
  }
}

// gtRegister
export const gtRegister = async(ctx, next) => {
  try{
    // challenge
    let res = await gtSlide.register(null)
    if(!res.success){
      //  failback
      ctx.session.failback = true
      ctx.body = res
    }else {
      // 
      ctx.session.failback = false
      ctx.body = res
    }
  }catch (e) {
    ctx.body = e
  }
}



